.el_picture06 {
  position: relative;
  padding-top: 6%;
  overflow: hidden;
  > img {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    height: 100%;
    object-fit: cover;
    object-position: center center;
  }
}

